<template>
	<div v-show="showFlag" class="food" transition="move" v-el:food>
		<div class="food-content">
			<div class="image-header">
				<img :src="food.image">
				<div class="back" @click="hide">
					<span class="icon-arrow_lift"></span>
				</div>
			</div>
			<div class="content">
				<h1 class="title">{{food.name}}</h1>
				<div class="detail">
					<span class="sell-count">月售{{food.sellCount}}</span>
					<span class="rating">好评率{{food.rating}}%</span>
				</div>
				<div class="price">
					<span class="now">￥{{food.price}}</span>
					<span v-show="food.oldPrice" class="old">￥{{food.oldPrice}}</span>
				</div>
				<div class="cartcontrol-wrapper">
					<cartcontrol :food="food"></cartcontrol>
				</div>
				<div @touchend.stop.prevent="addFirst($event)" class="buy" v-show="!food.count || food.count===0" transition="fade">加入购物车</div>
			</div>
			<split v-show="food.info"></split>
			<div class="info" v-show="food.info">
				<h1 class="title">商品信息</h1>
				<p class="text">{{food.info}}</p>
			</div>
			<split></split>
			<div class="rating">
				<h1 class="title">商品评价</h1>
				<ratingselect :select-type="selectType" :only-content="onlyContent" :desc="desc" :ratings="food.ratings"></ratingselect>
				<div class="rating-wrapper">
					<ul v-show="food.ratings && food.ratings.length">
						<li v-for="rating in food.ratings" class="rating-item border-1px" v-show="needShow(rating.rateType,rating.text)" >
							<div class="user">
								<span class="name">{{rating.username}}</span>
								<img width="12" height="12" :src="rating.avatar" class="avatar">
							</div>
							<div class="time">{{rating.rateTime|formatDate}}</div>
							<p class="text">
								<span :class="{'icon-thumb_up':rating.rateType===0,'icon-thumb_down':rating.rateType===1}"></span>{{rating.text}}
							</p>
						</li>
					</ul>
					<div v-else class="no-rating">暂无评价</div>
				</div>
			</div>
		</div>
	</div>
</template>

<script type="text/javascript">
	import Vue from 'vue';
	import {formatDate} from '../../common/js/date';
	import BScroll from 'better-scroll';
	import cartcontrol from 'components/cartcontrol/cartcontrol';
	import ratingselect from 'components/ratingselect/ratingselect';
	import split from 'components/split/split';
	const ALL = 2;
	export default{
		components: {
			cartcontrol,
			split,
			ratingselect
		},
		props: {
			food: {
				type: Object
			}
		},
		data() {
			return {
				showFlag: false,
				selectType: ALL,
				onlyContent: true,
				desc: {
					all: '全部',
					positive: '推荐',
					negative: '吐槽'
				}
			};
		},
		methods: {
			show() {
				this.showFlag = true;
				this.selecType = ALL;
				this.onlyContent = true;
				this.$nextTick(() => {
					if (!this.scroll) {
						this.scroll = new BScroll(this.$els.food, {
							click: true
						});
					} else {
						this.scroll.refresh();
					}
				});
			},
			hide() {
				this.showFlag = false;
			},
			addFirst(event) {
				this.$dispatch('cart.add',event.target);
				Vue.set(this.food,'count',1);
			},
			needShow(type,text) {
				if (this.onlyContent && !text) {
					return false;
				}
				if (this.selectType === ALL) {
					return true;
				} else {
					return type === this.selectType;
				}
			}
		},
		events: {
			'ratingtype.select'(type) {
				this.selectType = type;
				this.$nextTick(() => {
					this.scroll.refresh();
				});
			},
			'content.toggle'(onlyContent) {
				this.onlyContent = onlyContent;
				this.$nextTick(() => {
					this.scroll.refresh();
				});
			}
		},
		filters: {
			formatDate(time) {
				let date = new Date(time);
				return formatDate(date,'yyyy-MM-dd hh:mm');
			}
		}
	};
</script>

<style lang="scss" rel="stylesheet/scss" type="text/css">
	@import '../../common/sass/mixin';
	.food{
		position: fixed;
		left: 0;
		top: 0;
		bottom:48px;
		z-index: 30;
		width:100%;
		background:#fff;
		&.move-transition{
			transition: all 0.2s linear;
			transform: translate3d(0,0,0);
		}
		&.move-enter,&.move-leave{
			transform: translate3d(100%,0,0);
		}
		.image-header{
			position: relative;
			width:100%;
			height:0;
			padding-top:100%;
			img{
				position:absolute;
				top:0;
				left:0;
				width:100%;
				height:100%;
			}
			.back{
				position: absolute;
				top:10px;
				left:0;
				.icon-arrow_lift{
					display:block;
					padding:10px;
					font-size:20px;
					color:#fff;
				}
			}
		}
		.content{
			padding:18px;
			position:relative;
			.title{
				line-height:14px;
				margin-bottom:8px;
				font-size:14px;
				font-weight:700;
				color:rgb(7,17,27);
			}
			.detail{
				margin-bottom:18px;
				font-size:0;
				.sell-count,.rating{
					line-height:10px;
					font-size:10px;
					color:rgb(147,153,159);
				}
				.sell-count{
					margin-right:12px;
				}
			}
			.price{
				line-height:24px;
				.now{
					margin-right:8px;
					font-size: 14px;
					color:rgb(240,20,20);
					font-weight:700;
				}
				.old{
					text-decoration:line-through;
					font-size:10px;
					color:rgb(147,153,159);
				}
			}
			.cartcontrol-wrapper{
				position:absolute;
				right:12px;
				bottom:12px;
			}
			.buy{
				position:absolute;
				right:18px;
				bottom:18px;
				z-index:10;
				height:24px;
				line-height:24px;
				padding:0 12px;
				box-sizing:border-box;
				border-radius:12px;
				font-size:10px;
				color:#fff;
				background:rgb(0,160,220);
				&.fade-transition{
					transition:all 0.2s;
					opacity: 1;
				}
				&.fade-enter,&.fade-leave{
					opacity: 0;
				}
			}
		}
		.info{
			padding:18px;
			.title{
				line-height:14px;
				margin-bottom:6px;
				font-size:14px;
				color:rgb(7,17,27);
			}
			.text{
				line-height:24px;
				font-size:12px;
				padding:0 8px;
				color:rgb(77,85,93);
			}
		}
		.rating{
			padding-top:18px;
			.title{
				line-height:14px;
				margin-left:18px;
				font-size:14px;
				color:rgb(7,17,27);
			}
			.rating-wrapper{
				padding:0 18px;
				.rating-item{
					position:relative;
					padding:16px 0;
					@include border-1px(rgba(7,17,27,0.1))
					.user{
						position:absolute;
						right:0;
						top:16px;
						font-size:0;
						line-height:12px;
						.name{
							display:inline-block;
							vertical-align:top;
							font-size:10px;
							margin-right:6px;
							color:rgb(147,153,159)
						}
						.avatar{
							border-radius: 50%;
							vertical-align:top;
						}	
					}
					.time{
						margin-bottom:6px;
						line-height:12px;
						font-size:10px;
						color:rgb(147,153,159);
					}
					.text{
						line-height:16px;
						font-size:12px;
						color:rgb(7,17,27);
						.icon-thumb_up,.icon-thumb_down{
							margin-right:4px;
							line-height:16px;
							font-size:12px;
						}
						.icon-thumb_up{
							color:rgb(0,160,220);
						}
						.icon-thumb_down{
							color:rgb(147,153,159);
						}
					}
				}
				.no-rating {
					padding:16px 0;
					font-size:12px;
					color:rgb(147,153,159);	
				}
			}
		}
	}
</style>


