<template>
	<div class="seller" v-el:seller>
		<div class="seller-content">
			<div class="overview">
				<h1 class="title">{{seller.name}}</h1>
				<div class="desc border-1px">
					<star :size="36" :score="seller.score"></star>
					<span class="text">({{seller.ratingCount}})</span>
					<span class="text">月售{{seller.sellCount}}单</span>
				</div>
				<ul class="remark">
					<li class="block">
						<h2>起送价</h2>
						<div class="content">
							<span class="stress">{{seller.minPrice}}</span>元
						</div>
					</li>
					<li class="block">
						<h2>商家配送</h2>
						<div class="content">
							<span class="stress">{{seller.deliveryPrice}}</span>元
						</div>
					</li>
					<li class="block">
						<h2>平均配送时间</h2>
						<div class="content">
							<span class="stress">{{seller.deliveryTime}}</span>分钟
						</div>
					</li>
				</ul>
				<div class="favorite" @click="toggleFavorite($event)">
					<span class="icon-favorite" :class="{'active':favorite}"></span>
					<span class="text">{{favoriteText}}</span>
				</div>
			</div>
			<split></split>
			<div class="bulletin">
				<h1 class="title">公告与活动</h1>
				<div class="content-wrapper">
					<p class="content">{{seller.bulletin}}</p>
				</div>
				<ul v-if="seller.supports" class="supports">
					<li class="support-item border-1px" v-for="item in seller.supports">
						<span class="icon" :class="classMap[seller.supports[$index].type]"></span>
						<span class="text">{{seller.supports[$index].description}}</span>
					</li>
				</ul>
			</div>
			<split></split>
			<div class="pics">
				<h1 class="title">商家实景</h1>
				<div class="pic-wrapper" v-el:pic-wrapper>
					<ul class="pic-list" v-el:pic-list>
						<li class="pic-item" v-for="pic in seller.pics">
							<img :src="pic" width="120" height="90">
						</li>
					</ul>
				</div>
			</div>
			<split></split>
			<div class="info">
				<h1 class="title border-1px">商家信息</h1>
				<ul>
					<li class="info-item border-1px" v-for="info in seller.infos">{{info}}</li>
				</ul>
			</div>
		</div>
	</div>
</template>

<script type="text/javascript">
	import {saveToLocal, loadFromLocal} from 'common/js/store';
	import star from 'components/star/star';
	import split from 'components/split/split';
	import BScroll from 'better-scroll';
	export default{
		data() {
			return {
				favorite: (() => {
					return loadFromLocal(this.seller.id, 'favorite', false);
				})()
			};
		},
		computed: {
			favoriteText() {
				return this.favorite ? '已收藏' : '收藏';
			}
		},
		components: {
			star,
			split
		},
		props: {
			seller: {
				type: Object
			}
		},
		created() {
			this.classMap = ['decrease', 'discount', 'special', 'invoice', 'guarantee'];
		},
		watch: {
			'seller'() {
				this._initScroll();
				this._initPics();
			}
		},
		methods: {
			_initScroll() {
				if (!this.scroll) {
					this.scroll = new BScroll(this.$els.seller, {
						click: true
					});
				} else {
					this.scroll.refresh();
				}
			},
			_initPics() {
				if (this.seller.pics) {
					let picWidth = 120;
					let margin = 6;
					let width = (picWidth + margin) * this.seller.pics.length - margin;
					this.$els.picList.style.width = width + 'px';
					this.$nextTick(() => {
						if (!this.picScroll) {
							this.picScroll = new BScroll(this.$els.picWrapper, {
								click: true,
								scrollX: true,
								eventPassthrough: 'vertical'
							});
						} else {
							this.picScroll.refresh();
						}
					});
				}
			},
			toggleFavorite(event) {
				if (!event._constructed) {
					return;
				}
				this.favorite = !this.favorite;
				saveToLocal(this.seller.id, 'favorite', this.favorite);
			}
		}
	};
</script>

<style lang="scss" rel="stylesheet/scss" type="text/css">
	@import '../../common/sass/mixin';
	.seller{
		overflow:hidden;
		position:absolute;
		top:174px;
		left:0;
		bottom:0;
		width:100%;
		.overview{
			padding:18px;
			.title{
				font-size:14px;
				margin-bottom:8px;
				line-height:14px;
				color:rgb(7,17,27);
			}
			.desc{
				padding-bottom:18px;
				@include border-1px(rgba(7,17,27,0.1));
				font-size:0;
				.star{
					display:inline-block;
					vertical-align:top;
					margin-right:8px;
				}
				.text{
					display:inline-block;
					line-height:18px;
					vertical-align:top;
					margin-right:12px;
					font-size:10px;
					color:rgb(77,85,93);
				}
			}
			.remark{
				display:flex;
				padding-top:18px;
				.block{
					flex:1;
					text-align:center;
					border-right:1px solid rgba(7,17,27,0.1);
					&.last-child{
						border:none;
					}
					h2{
						font-size:10px;
						line-height:10px;
						margin-bottom:4px;
						color:rgb(147,153,159);
					}
					.content{
						line-height:24px;
						font-size: 10px;
						color: rgb(7,17,27);
						.stress{
							font-size: 24px;
						}
					}
				}
			}
			.favorite{
				position:absolute;
				right:14px;
				top:18px;
				text-align:center;
				width:45px;
				.icon-favorite{
					display:block;
					color:#d4d6d9;
					font-size:24px;
					line-height:24px;
					margin-bottom:4px;
					&.active{
						color:rgb(240,20,20)
					}
				}
				.text{
					font-size:10px;
					line-height:10px;
					color:rgb(77,25,93);
				}
			}
		}
		.bulletin{
			padding:18px 18px 0 18px;
			.title{
				font-size:14px;
				margin-bottom:8px;
				line-height:14px;
				color:rgb(7,17,27);
			}
			.content-wrapper{
				padding:0 12px 16px;
				@include border-1px(rgba(7,17,27,0.1));
				.content{
					line-height: 24px;
					font-size: 12px;
					color:rgb(240,20,20);
				}
			}
			.supports{
				.support-item{
					@include border-1px(rgba(7,17,27,0.1));
					padding:16px 12px;
					&:last-child{
						&:after{
							border:none;
						}
					}
					.icon{
						display: inline-block;
						vertical-align: top;
						width: 16px;
						height: 16px;
						margin-right: 6px;
						background-size:16px 16px;
						background-repeat:no-repeat;
						&.decrease{
							@include bg-image('decrease_4');
						}
						&.discount{
							@include bg-image('discount_4');
						}
						&.guarantee{
							@include bg-image('guarantee_4');
						}
						&.special{
							@include bg-image('special_4');
						}
						&.invoice{
							@include bg-image('invoice_4');
						}
					}
					.text{
						font-size: 12px;
						line-height: 16px;
						color:rgb(7,17,27);
					}
				}
			}
		}
		.pics{
			padding:18px 0 18px 18px;
			.title{
				font-size:14px;
				margin-bottom:12px;
				line-height:14px;
				color:rgb(7,17,27);
			}
			.pic-wrapper{
				width:100%;
				overflow:hidden;
				white-space:nowrap;
				.pic-list{
					font-size:0;
					.pic-item{
						display:inline-block;
						margin-right:6px;
						width:120px;
						height:90px;
						&:last-child{
							margin:0;
						}
					}
				}
			}
		}
		.info{
			padding: 18px 18px 0 18px;
			color:rgb(7,17,27);
			.title{
				font-size:14px;
				padding-bottom:8px;
				line-height:14px;
				@include border-1px(rgba(7,17,27,0.1));
			}
			.info-item{
				padding:16px 12px;
				line-height:16px;
				font-size:12px;
				@include border-1px(rgba(7,17,27,0.1));
				&:last-child:after{
					border:none;
				}
			}
		}
	}
</style>


